<template>
    <v-card class="defaultCard" color="bgtopology" elevation="0">

        <v-card-title class="pa-0">
            <span  class="titlePages">{{  t('analyticsPage.costmap') }}</span>
        </v-card-title>

        <v-sheet color="transparent" v-if="showCard">
            <v-img                 
            :class="xs?'legend_img_snr_xs':
            sm?'legend_img_snr_sm':
            md?'legend_img_snr_md':'legend_img_snr'"
            src="@/assets/leyenda/leyendacosto2.png"
            />
            <v-card-text class="text-center"> 
                <!-- GOOGLE MAPS-->   
                <GMapMap
                    v-if="markersrajant?.length !== 0"
                    :center="{lat: +30.968869, lng: -110.337264}"
                    :zoom="zoom"
                    map-type-id="satellite"
                    style="height: 700px"
                >    
                    <!-- COST MARKER -->
                    <GMapMarker 
                        :key="index"
                        v-for="(m, index) in finalArrayCost"
                        :position="m.position"
                        :icon="{
                            url: m.img,
                            scaledSize: m.size,
                            labelOrigin: m.tipo ===  CONSTSTRINGS.STRINGS_TYPES[7] || m.tipo === CONSTSTRINGS.STRINGS_TYPES[8] ? {x: 20, y: 70} : {x: 25, y: 35},
                        }"
                        :title=m.ip
                        :label="{
                            text:m.name,
                            className:'blackLabel',
                            fontSize:'10px'
                        }"  
                        @click="showInfo(m.ip??'',m.cost??0)"                
                    >    
                        <GMapInfoWindow
                            :closeclick="true"
                            @closeclick="showInfo('0',0)"
                            :opened="ipInfo === m.ip"
                        >
                            <CardInfo v-if="ipInfo === m.ip"  
                                :ip = "m.ip ? m.ip : ''"
                                :nameItem = "m.name??''"
                                :typeItem = "m.tipo??''"
                                :colorOne = colorSliderAlignmet
                                :colorTwo = colorSliderCost
                                :firstValue = latencyValue
                                :secondValue = "m.cost??0"
                                :isCost = 'true'
                            />
                        </GMapInfoWindow>                    
                    </GMapMarker>

                </GMapMap>

                <Prog_circular v-else/>
            </v-card-text>
        </v-sheet>

        <v-row  v-if="showError" class="ma-0 px-4 py-3">
            <h3> {{t('noInformation')}} </h3>
        </v-row>

    </v-card>
</template>

<script setup lang="ts">
import axios from 'axios';
import { computed, onMounted, ref, Ref } from 'vue';
import { useDisplay }       from 'vuetify';
import { useI18n }          from 'vue-i18n';

// Component
import CONSTAPIS            from '@/constants/apis';
import CONSTSTRINGS            from '@/constants/strings';
import Prog_circular        from '@/components/PrincipalComponents/Prog_circular.vue';
import CardInfo             from '@/components/SmartLink/maps/CardInfo.vue';

// Interfaces
import { IMG_SIZE }         from '@/interfaces/SmartLink/Imgs';
import { STRINGS }          from '@/interfaces/SmartLink/General';
import { MARKERS }          from '@/interfaces/SmartLink/Map';
import { EQUIPAMENTS }      from '@/interfaces/SmartLink/Equips'

        //==================================================//
        //==================== VARIABLES ===================//
        //==================================================//

        /**
         * Vue / Vuetify
         */
        const { t }                                 = useI18n();
        const { xs, sm, md }                        = useDisplay();

        /**
         * Component
         */
        const elementRAJANT:Ref<EQUIPAMENTS[]>      = ref([]);
        const markersrajant:Ref<MARKERS[]>          = ref([]);
        const zoom                                  = ref(14);        
        const latencyValue                          = ref(0)
        const ipInfo                                = ref();
        const costValue                             = ref(0);

        const showError                             = ref(false);
        const showCard                              = ref(false);

        /**
         * COST Component
         */
        const cost:Ref<EQUIPAMENTS[]>               = ref([]);
        const finalArrayCost:Ref<MARKERS[]>         = ref([]);

        /**
         * Constants
         */    
        const POST_EQUIP_IMG:STRINGS = {
            'Solar Trailer'     : 'apt.png',
            'Tower'             : 'ap.png',
            'Shovel'            : 'pala.png',
            'Haultruck'         : 'cliente.png',
            'Driller'           : 'drill.png',
            'Front Loader'      : 'cargadores.png',
            'Watertruck'        : 'watertruck.png',
        }

        const EQUIPMENT_SIZE:IMG_SIZE = {
            'Solar Trailer'     : {width: 40, height: 60},
            'Tower'             : {width: 40, height: 60},
            'Shovel'            : {width: 50, height: 30},
            'Haultruck'         : {width: 40, height: 30},
            'Driller'           : {width: 40, height: 30},
            'Front Loader'      : {width: 40, height: 30},
            'Watertruck'        : {width: 40, height: 30},
        }

        //==================================================//
        //===================== METHODS ====================//
        //==================================================//
        /**
         * Show info when click in Item in the Map
         */
        const showInfo = async (ip:string,cost:number) => {
            if(ip === '0') {
                ipInfo.value = '0';
                costValue.value = 0
            }  
            if(ip !== '0' ){
                
                ipInfo.value        = ip;
                costValue.value     = cost;
                // await getOperabilityData(ip);
                await axios.get(CONSTAPIS.APIS.topology.getLatencyByIP + ip).then((res)=>{
                    res.data?.length !== 0 ?  latencyValue.value = res.data[0].latency : latencyValue.value = 0
                })
            }            
        }


        /**
         * Return Options with custom Color
         */
        const colorSliderAlignmet  = computed(()=>{
        let colorValue;
        
            if(latencyValue.value >= 0      && latencyValue.value <  100)   colorValue = ['#00FF00','#80FF00']
            if(latencyValue.value >= 100    && latencyValue.value <  200)   colorValue = ['#00FF00','#80FF00','#FFFF00']
            if(latencyValue.value >= 200    && latencyValue.value <= 500 )  colorValue = ['#00FF00','#80FF00','#FFFF00','#FBA977','#FF0000']
            
            return colorValue
       })

        const colorSliderCost  = computed(()=>{
        let colorValue;
        
            if(costValue.value >= 0      && costValue.value <=  5000 )   colorValue = ['#00FF00','#80FF00']
            if(costValue.value >  5000   && costValue.value <=  10000)   colorValue = ['#FFFF00','#FFFF00']
            if(costValue.value >  10000 )                                colorValue = ['#FF0000','#FF0000']
            
            return colorValue
       })


        /**
         *  Get GPS location to RAJANT equipment
         */
        const getLatLongRAJANT = async () => {
            await axios.get(CONSTAPIS.APIS.maps.getAllLatLngRAJ).then((response)=>{
                 elementRAJANT.value = response.data;

                 elementRAJANT.value.forEach((element:EQUIPAMENTS) => {
                     if(element.tipo !== CONSTSTRINGS.STRINGS_TYPES[11]){
                        
                        let gradoLat    = parseFloat((element.latitud??'0').slice(0,2));
                        let minuteLat   = (parseFloat((element.latitud??'0').slice(2,-1))/60);
                        let lat         = gradoLat + minuteLat;
                    
                        let gradoLng    = parseFloat((element.longitud??'0').slice(0,3));
                        let minuteLng   = (parseFloat((element.longitud??'0').slice(3,-1))/60);
                        let lng         = (gradoLng + minuteLng) * (-1);

                        markersrajant.value.push({
                            position: {
                                lat: parseFloat(lat.toFixed(6)), lng: parseFloat(lng.toFixed(6))
                            },
                            label   : '',
                            name    : element.hostname,
                            ip      : element.ip,
                            tipo    : element.tipo,
                            img     : '',
                            size    : EQUIPMENT_SIZE[element.tipo ? element.tipo : ''],
                        })
                     }
                })              
            })
        }



        const getCostData = async() => {

            await axios.get(CONSTAPIS.APIS.instamesh.getCostData).then((elem)=>{
                cost.value = elem.data;
            });

            await matchCostWEquip()
        }


        const matchCostWEquip = async() => {

            cost.value.forEach((cost:EQUIPAMENTS) => {

                let objCost:MARKERS = {
                    position    : {lat:0, lng:0},
                    cost        : 0,
                    img         : '',
                    size        : {width:0, height:0},
                    tipo        : '',
                    ip          : '',
                    name        : ''
                }

                markersrajant.value?.find(({ip,position,size,tipo,name}:MARKERS) => {
                    
                    if(cost.ip === ip && cost.costo){

                        let preImg = '';
                       
                        objCost.position    = position;
                        objCost.size        = size;
                        objCost.cost        = cost.costo;
                        objCost.tipo        = tipo;
                        objCost.ip          = ip;
                        objCost.name        = name;
                        
                        if(cost.costo <= 5000  && cost.costo >= 0   ) preImg = require("@/assets/Img/Green/"+POST_EQUIP_IMG[tipo ? tipo : ''])
                        if(cost.costo <= 10000 && cost.costo >= 5000) preImg = require("@/assets/Img/Yellow/"+POST_EQUIP_IMG[tipo ? tipo : ''])
                        if(cost.costo >  10000)                       preImg = require("@/assets/Img/Red/"+POST_EQUIP_IMG[tipo ? tipo : ''])
                        
                        objCost.img = preImg;
                        finalArrayCost.value.push(objCost);
                    }
                })
            })

            if(finalArrayCost.value.length > 0) { 
                showError.value         = false; 
                showCard .value         = true; 
            }
            else{
                showError.value         = true; 
                showCard .value         = false;
            }
           
        }


        onMounted(async()=>{

            await getLatLongRAJANT();
            await getCostData();
           
        })

</script>

<style>
.blackLabel{
    color: black !important;
    font-weight: 1000;
}
</style>