<template>
    <div v-if="showChart">
        <Chart :constructorType="'stockChart'" class="hc" :options="chartOptions" ref="chart"></Chart>
    </div>
</template>

<script setup lang="ts">
import { ref, Ref, computed, onMounted }    from 'vue';
import { Chart }            from 'highcharts-vue';
import { useTheme }         from 'vuetify';
import { getTimeZone } from '@/functions/project';

const props = defineProps({
    arrayItems: { required:true, type: Array }, arrayNames: { required:true, type: Array},
    title: { required:true }
})
const theme                 = useTheme();
const colorBG               = computed(() => theme.global.current.value.colors.bgtopology);
const colorText             = computed(() => theme.global.current.value.colors.colorText);
const finalARRAYITEMS       = ref(props.arrayItems);
const finalARRAYNAMES       = ref(props.arrayNames);
const finalObjs:Ref<any[]>         = ref([]);
const showChart = ref(false)

/* eslint-disable */
const chartOptions:any = ref({
    chart: {
        backgroundColor: colorBG,
        style:{ color: colorText }
    },
    legend: {
        enabled: false,
        align: 'center',
        layout: 'horizontal',
        verticalAlign: 'bottom',
        style:{ color: colorText }
    },
    title: {
        text: props.title,
        style:{ color: colorText }
    },
    xAxis: {
        labels: {
            style:{ color: colorText }
        }
    },
    credits: {
        enabled: false
    },
    tooltip: {
        shared: true,
        style: { color: 'black' }, 
    },
    series: finalObjs,
    rangeSelector: {
        allButtonsEnabled: true,
        buttons: [
            { type: 'day',      count: 1,   text: '1 Day'   }, 
            { type: 'day',      count: 2,   text: '2 Day'   },
            { type: 'week',     count: 1,   text: '1 Week'  }, 
            { type: 'month',    count: 1,   text: '1 Month' },
        ],
        buttonTheme: { width: 60 },
        selected: 2
    },
    exporting: { enabled: false },
    time: {
        timezone: getTimeZone()
    }
})     

const getAllData = async() => {
    for(let i=0; i<finalARRAYITEMS.value.length; i++ ){
        finalObjs.value.push(
            {
                name: finalARRAYNAMES.value[i],
                data: finalARRAYITEMS.value[i],
                tooltip: { valueDecimals: 2 }
            },
        )
    }
}


onMounted(async () => {
    await getAllData();
    showChart.value = true;
})
</script>