import { ref } from "vue";

export function useTypingEffect(text:string, speed = 5) {
  const displayedText = ref("");
  let interval: ReturnType<typeof setInterval> | null = null// Usamos `ref` para hacerlo reactivo

  const startTyping = () => {
    displayedText.value = "";
    let currentIndex = 0;

    // Limpiar el intervalo anterior si existe
    if (interval !== null) {
      clearInterval(interval);
      interval = null; // Restablecer a null después de limpiar
    }

    // Iniciar un nuevo intervalo
    interval = setInterval(() => {
      if (currentIndex < text.length) {
        displayedText.value += text[currentIndex];
        currentIndex++;
      } else {
        // Detener el intervalo cuando se complete el texto
        if (interval !== null) {
          clearInterval(interval);
          interval = null; // Restablecer a null
        }
      }
    }, speed);
  };


  return { displayedText, startTyping };
}