<template>
    <div>
        <Chart :constructorType="'stockChart'" class="hc" :options="finalChartOpcions" ref="chart"></Chart>
    </div>
</template>

<script setup lang="ts">
import { ref, computed }    from 'vue';
import {Chart}              from 'highcharts-vue';
import { useTheme }         from 'vuetify';
import { getTimeZone } from '@/functions/project';

// CONSTANTS
import CONSTGRAPHS          from '@/constants/graphs';

const props = defineProps({
    firstArray      : { required:true },
    secondArray     : { required:true },
    namePrimary     : { required:true },
    nameSecundary   : { required:true },
    colorFirst      : { required:true },
    colorSecond     : { required:true },
    title           : { required:true, type:String },
    subtitle        : { required:true, type:String },
    typeChart       : { required:true, type:String }
})

const theme         = useTheme();

const potLine       = ref(CONSTGRAPHS.POTLINE[props.typeChart]);
const zones         = ref(CONSTGRAPHS.ZONES[props.typeChart]);
const colorBG       = computed(() => theme.global.current.value.colors.bgtopology);
const colorText     = computed(() => theme.global.current.value.colors.colorText);
const yAxisTitle    = ref(CONSTGRAPHS.YAXISLABEL['predict'] || '');

/* eslint-disable */
const chartOptions:any = ref({
    chart:{
        backgroundColor: colorBG,
        style:{ color: colorText }
    },
    title: {
        text: props.title,
        style:{ color: colorText }
    },
    subtitle: {
        text: props.subtitle,
        style:{ color: colorText }
    },
    labelStyle:{
        style:{ color: colorText }
    },
    credits: { enabled: false },
    xAxis: {
        labels: {
            style:{ color: colorText }
        }
    },
    yAxis: {
        title: {
            text: yAxisTitle,
            style:{ color: colorText }
        },
        plotLines: [ {
            value: potLine,
            color: 'red',
            dashStyle: 'shortdash',
            width: 2,
            label: {
                text: props.typeChart == 'predict' ? '-75dBm' : '20dBm',
                style:{ color: colorText }
            }
        },
    ],
    },
    tooltip:{
        style: { color: 'black' }
    },
    series: [
        {
            name: props.namePrimary,
            data: props.firstArray,
            color: props.colorFirst,
            zones: zones.value,
            tooltip: { valueDecimals: 2 }
        },
        {
            name: props.nameSecundary,
            data: props.secondArray,
            color: props.colorSecond,
            zones: zones.value,
            tooltip: { valueDecimals: 2 }
        }
    ],
    rangeSelector: {
        allButtonsEnabled: true,
        buttons: [
            { type: 'day',      count: 1,   text: '1 Day'   }, 
            { type: 'day',      count: 2,   text: '2 Day'   },
            { type: 'week',     count: 1,   text: '1 Week'  }, 
            { type: 'month',    count: 1,   text: '1 Month' },
        ],
        buttonTheme: { width: 60 },
        selected: 0
    },
    exporting: { enabled: false },
    time: {
        timezone: getTimeZone()
    }
})    

const finalChartOpcions = computed(() => chartOptions.value)
</script>