<template>
  <div class="typingText">
    <p v-html="displayedText"></p>
    <!-- <p>{{ displayedText }}</p> -->
  </div>
</template>

<script setup lang="ts">
import { onMounted } from "vue";
import { useTypingEffect } from "@/functions/useTypingEffect";
  
const props = defineProps({
    text       : { type: String, required: true},
    speed      : { type: Number, default: 10 }
})
const { displayedText, startTyping } = useTypingEffect(props.text, props.speed);

onMounted(() => {
  startTyping();
});

  </script>
  
  <style>
  .typingText p {
    /* font-family: monospace; */
    font-size: 1.1rem;
  }
  </style>