<template>
    <div>
        <Chart :constructorType="'stockChart'" class="hc" :options="finalChartOpcions"></Chart>
    </div>
</template>

<script setup lang="ts">
import { ref, computed }    from 'vue';
import {Chart}              from 'highcharts-vue';
import { useTheme }         from 'vuetify';
import { useI18n }          from 'vue-i18n';

import { getTimeZone } from '@/functions/project'

// CONSTANTS
import CONSTGRAPHS          from '@/constants/graphs';
import CONSTSTRINGS         from '@/constants/strings';

// INTERFACES
interface Tooltip {
  formatter?: (this: { value?: number, y?:number, x?:number }) => string;
}
 
const props = defineProps({
    firstArray:{ required:true, type:Array },
    name:{ type:String, required:true },
    title:{ required:true },
    color:{ required:true },
    isactive:{ type:Boolean, default: false }
})
        
        /**
         * Vue / Vuetify
         */

// const chartSingle:any       = ref()
const { t }                 = useI18n();
const theme                 = useTheme();

const colorBG               = computed(() => theme.global.current.value.colors.bgtopology)
const colorText             = computed(() => theme.global.current.value.colors.colorText)
const zones                 = ref(CONSTGRAPHS.ZONES[props.name] || []);
const potLine               = CONSTGRAPHS.POTLINE[props.name];

const principalData         = computed(() => props.firstArray)
const isGraphActive         = computed(() => props.isactive)

const type                  = ref(CONSTGRAPHS.TYPE_GRAPH[props.name] || null);
const reversed              = ref(props.name === CONSTSTRINGS.STRINGS_HTML.rssi || props.name === CONSTSTRINGS.STRINGS_HTML.rsrp || props.name === CONSTSTRINGS.STRINGS_HTML.rsrq ? true : false)
const yAxisTitle            = ref(CONSTGRAPHS.YAXISLABEL[props.name] || '');
const yourLabels            = [ t('labels.down'), t('labels.alarm'), t('labels.alert'), t('labels.ok')];
const labels:Tooltip        = props.name === 'Status' ? 
{
    formatter: function(){
        return yourLabels[this.value??0];
    }
} : 
{};

const dataGrouping = props.name === CONSTSTRINGS.STRINGS_HTML.Status || props.name === CONSTSTRINGS.STRINGS_HTML.rssi || props.name === CONSTSTRINGS.STRINGS_HTML.rsrp || props.name === CONSTSTRINGS.STRINGS_HTML.rsrq ? {
approximation: 'average', 
enabled: true,
} : {};

const tooltip:Tooltip =  props.name === CONSTSTRINGS.STRINGS_HTML.Status ? {
    formatter: function(){

        const valor         = this.x ? this.x + (300 * 60 * 1000) : 0;
        const newDate       = new Date(valor as number);
        const partsOfDate   = newDate.toString().split(' ');

        const finalDate = `${partsOfDate[1]} ${partsOfDate[2]} ${partsOfDate[3]} ${partsOfDate[4]}`; 

        var label;
        
        switch(true){
            case ((this.y??0) >= 0 && (this.y??0) < 1):
                label = t('labels.down');
                break;
            case ((this.y??0) >= 1 && (this.y??0) < 2):
                label = t('labels.alarm');
                break;
            case ((this.y??0) >= 2 && (this.y??0) < 3):
                label = t('labels.alert');
                break;
            case ((this.y??0) == 3):
                label = t('labels.ok');
                break;
        }
        return '<b> Valor: ' + label + '<br/>Fecha: ' + finalDate;
        // return '<b> Valor: ' + label ;
    },
    
} : props.name === 'cpu'  ? {
    formatter: function(){
        return t('serverPage.virtMach.cpu') + `: ` + (this.y??0) + ' %' ;
    }
} : props.name === 'memoria' ? {
    formatter: function(){
        return t('serverPage.virtMach.memo') + `: ` + (this.y??0) + ' %' ;
    }
} : props.name === 'disco' ? {
    formatter: function(){ 
        return t('serverPage.virtMach.disc') + `: ` + (this.y??0) + ' %' ;
    }
} : {};

/* eslint-disable */
const chartOptions:any = ref({

    chart:{
        backgroundColor: colorBG,
        style:{ color: colorText },
        zooming: { 
            mouseWheel: 
                { enabled: isGraphActive.value  }
         }
    },
    title: {
        text: props.title,
        style:{ color: colorText }
    },
    credits: {
        enabled: false
    },
    xAxis: {
        // type: 'datetime',
        labels: {
            style:{ color: colorText }
        },
        // timezone: 'America/New_York'
    },
    yAxis: {
        reversed:reversed,
        title: {
            text: yAxisTitle,
            style:{ color: colorText }
        },
        plotLines: [ {
            value: potLine,
            color: 'red',
            dashStyle: 'shortdash',
            width: 2,
            label: { text:  props.name === 'Latency' ? '100ms' : potLine + ' V' }
        }],
        labels: 
            labels,
            style: { color: colorText }
    },
    tooltip:
        tooltip,
        style: { color: 'black' },
    series: [
        {
            type: type,
            name: props.name,
            data: principalData.value,
            color:props.color,
            zones:zones.value,
            dataGrouping:dataGrouping,
            tooltip: {
                    valueDecimals: 2,
                    formatter:tooltip,
            },
            linecap: 'round'
        }
    ],
    rangeSelector: {
        allButtonsEnabled: true,
        buttons: [
            { type: 'day',      count: 1,   text: '1 Day'   }, 
            { type: 'day',      count: 2,   text: '2 Day'   },
            { type: 'week',     count: 1,   text: '1 Week'  }, 
            { type: 'month',    count: 1,   text: '1 Month' },
        ],
        buttonTheme: { width: 60 },
        selected: 0
    },
    exporting: { enabled: false },
    time: {
        timezone: getTimeZone()
    }
})

const finalChartOpcions = computed(() => chartOptions.value)

</script>

<style>
.highcharts-range-selector-group .highcharts-label text,
.highcharts-yaxis-labels text{
    color: rgb(var(--v-theme-colorText)) !important;
    fill: rgb(var(--v-theme-colorText)) !important;
}

</style>