<template>
    <div>
        <Chart :class="isactive ? 'borderActive' : ''" :constructorType="'stockChart'" class="hc" :options="finalChartOpcions" ref="chart"></Chart>
    </div>
</template>

<script setup lang="ts">
import { ref, computed }    from 'vue';
import {Chart}              from 'highcharts-vue';
import { useTheme }         from 'vuetify';
import { useI18n }          from 'vue-i18n';

import { getTimeZone } from '@/functions/project';
import CONSTCOLORS from '@/constants/colors'
 
const props = defineProps({
    array1:{ required:true, type:Array },
    array2:{ required:true, type:Array },
    array3:{ required:true, type:Array },
    array4:{ required:true, type:Array },
    isactive:{ type:Boolean, default: false }
})
        
const { t }                 = useI18n();
const theme                 = useTheme();

// const isGraphActive         = computed(() => props.isactive)
const colorBG               = computed(() => theme.global.current.value.colors.bgtopology)
const colorText             = computed(() => theme.global.current.value.colors.colorText)

const data1         = computed(() => props.array1);
const data2         = computed(() => props.array2);
const data3         = computed(() => props.array3);
const data4         = computed(() => props.array4);

const oksLabel       = computed(() =>  t('labels.ok_pl'));
const aleLabel       = computed(() =>  t('labels.alert_pl'));
const alaLabel       = computed(() =>  t('labels.alarm_pl'));
const dowLabel       = computed(() =>  t('labels.down_pl'));

/* eslint-disable */
const chartOptions:any = ref({

    chart:{
        type: 'area',
        height: 460,
        backgroundColor: colorBG,
        style:{ color: colorText },
    },
    legend: {
        enabled: true,
        align: 'center',
        layout: 'horizontal',
        verticalAlign: 'bottom',
        style:{ color: colorText }
    },
    credits: {
        enabled: false
    },
    xAxis: {
        labels: {
            style:{ color: colorText }
        },
    },
    plotOptions: {
        area: {
            stacking: 'normal',
            lineColor: '#666666',
            lineWidth: 1,
            marker: {
                lineWidth: 1,
                lineColor: '#666666'
            }
        }
    },
    series: [
        {
            name: dowLabel.value,
            data: data4.value,
            color:CONSTCOLORS.COLOR_HTML.grey,
            // linecap: 'round',
            // marker: { enabled: true }
        },
        {
            name: alaLabel.value,
            data: data2.value,
            color: CONSTCOLORS.COLOR_HTML.red,
            // linecap: 'round',
            // marker: { enabled: true }
        },
        {
            name: aleLabel.value,
            data: data3.value,
            color: CONSTCOLORS.COLOR_HTML.yellow,
            // linecap: 'round',
            // marker: { enabled: true }
        },
        {
            name: oksLabel.value,
            data: data1.value,
            color: CONSTCOLORS.COLOR_HTML.green,
            // linecap: 'round',
            // marker: { enabled: true },
            visible: false
        },
    ],
    rangeSelector: {
        allButtonsEnabled: true,
        buttons: [
            { type: 'day',      count: 1,   text: `1 ${t('labels.day')}`}, 
            { type: 'day',      count: 2,   text: `2 ${t('labels.day')}s` },
            { type: 'week',     count: 1,   text: `1 ${t('labels.week')}` }, 
            { type: 'month',    count: 1,   text: `1 ${t('labels.month')}` },
        ],
        buttonTheme: { width: 60 },
        selected: 0
    },
    exporting: { enabled: false },
    time: {
        timezone: getTimeZone()
    }
})

const finalChartOpcions = computed(() => chartOptions.value)

</script>
<style>
.highcharts-legend-item text{
    fill: rgb(var(--varColorText)) !important;
}
.borderActive{
    border: 3px solid rgba(255,255,255,.8);
    border-radius: 20px;
}
</style>