<template>
    <v-sheet class="defaultCard" color="bgtopology">
        <span  class="titlePages">{{title}}</span>
        <v-row class="ma-0 mt-8" >
                <v-sheet color="transparent" :style="smAndDown ?'width: 100%' :`width: calc(100% - ${width}px)`" >   
                    <v-range-slider
                    v-if="showRangeSlider"
                    v-model="modelSliderStatus"
                    :max="12"
                    :min="0"
                    :step="1"
                    class="align-center mb-6"
                    hide-details
                    thumb-label
                    >
                        <template v-slot:thumb-label="{ modelValue }">
                            <v-sheet color="transparent" width="150"> 
                                {{ seasonStatus(modelValue) }}
                            </v-sheet>
                        </template>
                    </v-range-slider>
                    <div id="map"></div>
                </v-sheet>


                <v-col class="pa-0" v-if="showFilters" cols="12" sm="12" md="12" lg="2" xl="2" xxl="2">

                    
                    <v-sheet color="transparent" class="pa-4 pr-0">
                        <v-row class="ma-0">
                            <v-col cols="12" class="pa-0">
                            <v-select
                            v-model="haulTruckSelected"
                            :items="computedListTruck"
                            item-title="title"
                            item-value="value"
                            density="compact"
                            variant="outlined"
                            hide-details
                            />
                            <v-btn class='mt-4' color="success" :disabled="haulTruckSelected===''" block @click="getHaulTruckPositions">{{  t('buttons.searchTruck') }}</v-btn>
                            <!-- <v-btn class='mt-4' color="success" :disabled="haulTruckSelected===''" block @click="testPrueba">{{  t('buttons.searchTruck') }}</v-btn> -->
                            </v-col>
                        </v-row>
                        <v-row v-if="showError">
                            <v-col>No hay informacion</v-col>
                        </v-row>
                        <v-row class="ma-0" v-if="showCheckBoxes">
                            <v-col class="pa-0" cols="12">
                                <v-checkbox
                                v-model="selectedRSRP"
                                color="green"
                                label="RSRP"
                                hide-details
                                ></v-checkbox>
                                <v-checkbox
                                v-model="selectedRSRQ"
                                color="green"
                                label="RSRQ"
                                hide-details
                                ></v-checkbox>
                                <v-checkbox
                                v-model="selectedRSSI"
                                color="green"
                                label="RSSI"
                                hide-details
                                ></v-checkbox>
                                <v-checkbox
                                v-model="selectedSNR"
                                color="green"
                                label="SNR"
                                hide-details
                                ></v-checkbox>
                                <v-checkbox
                                v-model="selectedLATENCIA"
                                color="green"
                                label="Latencia"
                                hide-details
                                ></v-checkbox>
                            </v-col>
                        </v-row>
                    </v-sheet>
                </v-col>
            </v-row>
    </v-sheet>
</template>

<script setup lang="ts">
import { watch, ref, Ref, onMounted, ComputedRef, computed} from 'vue';
import { useStore }     from 'vuex';
import axios            from 'axios';
import { useDisplay }   from 'vuetify';
import { useI18n }      from 'vue-i18n';
import Papa from "papaparse";

import maplibregl from 'maplibre-gl';
import 'maplibre-gl/dist/maplibre-gl.css';

//CONSTANTS
import CONSTAPIS        from '@/constants/apis';

// COMPONENTS

// INTERFACES
import { CIRCLE, TRACKING } from '@/interfaces/SmartLink/Map';
import { STRINGS }          from '@/interfaces/SmartLink/General';
import { TOPO_ITEMS }       from '@/interfaces/SmartLink/Topology';
import { EQUIPAMENTS } from '@/interfaces/SmartLink/Equips';

        

const { t }                                 = useI18n();
const { smAndDown }                         = useDisplay();
const width                                 = ref('350');
watch(smAndDown,(valor:boolean) => {
    valor ? width.value = '100%' :  width.value = '350';
})

// const store                                 = useStore();

const TITLE_DRIVE:ComputedRef<STRINGS>      =  computed(() => {
    return {
        drive         : 'Drive',
        driveraj      : 'Drive Rajant',
    }
});


/**
 * Get PMPAP Data
 */


const infoWindowVisible     = ref(false);

// const getValueStoreDRIVE    = computed(() => store.state.valueDRIVE );
const getValueStoreDRIVE    = ref('drive');
const title                 = computed(() => TITLE_DRIVE.value[getValueStoreDRIVE.value]??'Title');

const showFilters           = ref(true);

const haulTruckSelected     = ref();
const indexFrom             = ref(0);
const indexTo               = ref(12);
const computedListTruck     = computed(() => finalListTruck.value)
const haulTrucksList:Ref<TOPO_ITEMS[]>      = ref([]);
const finalListTruck:Ref<TOPO_ITEMS[]>      = ref([]);

const showRangeSlider                   = ref(false);
const circles:Ref<any[]>             = ref([]);
const allCircles:Ref<any[]>          = ref([]);
const allCirclesFiltered:Ref<any[]>  = ref([]);
const selectedRSRP:Ref<boolean>         = ref(true);
const selectedRSRQ:Ref<boolean>         = ref(false);
const selectedRSSI:Ref<boolean>         = ref(false);
const selectedSNR:Ref<boolean>          = ref(false);
const selectedLATENCIA:Ref<boolean>     = ref(false);
const showError                         = ref(false);
const arrayDatesCircle:Ref<string[]>    = ref([]);
const modelSliderStatus:Ref<number[]>   = ref([0,12]);
const showCheckBoxes                    = ref(false);
const disableSearchTruck                = ref(true);

const getColorByRSRP = (rsrp:number) => {
    let finalcolor = '#0047AB';

    if(rsrp >= -80 ) { finalcolor = '#0047AB'; }
    if(rsrp >= -90 && rsrp < -80 ) { finalcolor = '#388E3C'; }
    if(rsrp >= -100 && rsrp < -90 ) { finalcolor = '#FFD700'; }
    if(rsrp < -100 ) { finalcolor = '#E43D30'; }

    return finalcolor
}
const getColorByRSRQ = (rsrq:number) => {
    let finalcolor = '#0047AB';

    if(rsrq >= -10 ) { finalcolor = '#0047AB'; }
    if(rsrq >= -15 && rsrq < -10 ) { finalcolor = '#388E3C'; }
    if(rsrq >= -20 && rsrq < -15 ) { finalcolor = '#FFD700'; }
    if(rsrq < -20 ) { finalcolor = '#E43D30'; }

    return finalcolor
}
const getColorBySNR = (snr:number) => {
    let finalcolor = '#0047AB';

    if(snr >= 20 ) { finalcolor = '#388E3C'; }
    if(snr >= 16 && snr < 20 ) { finalcolor = '#FFD700'; }
    if(snr >= 0 && snr < 16) { finalcolor = '#E43D30'; }

    return finalcolor
}
const getColorByLATENCIA = (latencia:number) => {
    let finalcolor = '#0047AB';

    // if(latencia >= -10 ) { finalcolor = '#0047AB'; }
    if(latencia > 0 && latencia <= 100 ) { finalcolor = '#388E3C'; }
    if(latencia > 100 && latencia <= 200 ) { finalcolor = '#FFD700'; }
    if(latencia > 200 && latencia <= 500 ) { finalcolor = '#E43D30'; }

    return finalcolor
}

const finalRSRPArray:any    = ref([]);
const finalRSRPObject:any   = ref({});
const finalRSRQArray:any    = ref([]);
const finalRSRQObject:any   = ref({});
const finalRSSIArray:any    = ref([]);
const finalRSSIObject:any   = ref({});
const finalSNRArray:any    = ref([]);
const finalSNRObject:any   = ref({});
const finalLATENCIAArray:any    = ref([]);
const finalLATENCIAObject:any   = ref({});

const getFinalPositions = (lat:number, lng:number) => {
    let lat1 = lat + 0.001;
    let lng1 = lng + 0.001;
    let finalItem = [
        [ lng,  lat  ],
        [ lng1, lat  ],
        [ lng1, lat1 ],
        [ lng,  lat1 ],
        [ lng,  lat  ],
    ]
    return finalItem
}

const getAllHaulTrucks = async() => {
    await axios.get(CONSTAPIS.APIS.drive.geAllIPDrive).then((response) => {
        let finalItems = response.data.body;
        finalItems.forEach((item:string) => {
            let prevIP = item.split('.')
            let finalIP = `${prevIP[0]}.${prevIP[1]}.${prevIP[2]}.${prevIP[3]}`
            haulTrucksList.value.push({
                title   : finalIP,
                value   : finalIP,
            })
        })
    })
    finalListTruck.value = haulTrucksList.value;
}


const getHaulTruckPositions = async() => {
    clearRSRPLayerSource();
    clearRSRQLayerSource();
    clearRSSILayerSource();
    clearSNRLayerSource();
    clearLATENCIALayerSource();
    selectedRSRP.value = true;
    selectedRSRQ.value = false;
    selectedRSSI.value = false;
    selectedSNR.value = false;
    selectedLATENCIA.value = false;
    showError.value = false;
    showRangeSlider.value = false;
    showCheckBoxes.value = false;
    circles.value = [];
    finalRSRPArray.value = [];
    finalRSRPObject.value = {};  
    let params = {
        ip : haulTruckSelected.value
    }
    
    let api = CONSTAPIS.APIS.drive.getInfoDriveByIP;
    await axios.get(api,{params:params}).then(async(response)=>{
    
        allCircles.value            = [];
        allCirclesFiltered.value    = [];
        modelSliderStatus.value     = [0,12];

        let finalItems:any = Papa.parse(response.data.body, {
            header: true, // Usa la primera fila como nombres de las claves en el JSON
            skipEmptyLines: true, // Ignorar líneas vacías
        });

        if(finalItems.data.length > 0) {
            let index = 0
            // finalItems.forEach((item:TRACKING) => {
            finalItems.data.forEach((item:any) => {
                index = index + 1;
                
                let lat   = 30.972732;
                let lng   = -110.34928166;

                if(item.latitud && item.latitud !== '') { lat = parseFloat(item.latitud??'0'); }
                if(item.longitud && item.longitud !== '') { lng = parseFloat(item.longitud??'0'); }
                if(item.gpsLat && item.gpsLat !== '') { lat = parseFloat(item.gpsLat??'0'); }
                if(item.gpsLong && item.gpsLong !== '') { lng = parseFloat(item.gpsLong??'0'); }

                let rsrp = item.rsrp === '' ? 0 : parseFloat(item.rsrp);
                let latencia = item.latencia !== '' && item.latencia ? parseFloat(item.latencia)   : 0 ;
                let snr = item.snr === '' ? 0 : parseFloat(item.snr);
                let rssi = item.rssi === '' ? 0 : parseFloat(item.rssi);
                let rsrq = item.rsrq === '' ? 0 : parseFloat(item.rsrq);

                let colorrsrp = getColorByRSRP(rsrp);
                let colorrsrq = getColorByRSRQ(rsrq);
                let colorrssi = getColorByRSRQ(rssi);
                let colorsnr = getColorBySNR(snr);
                let colorlate = getColorByLATENCIA(latencia);

                postPORTItems('rsrp',lat, lng, colorrsrp, `RSRP: ${rsrp}dBm`,index);
                postPORTItems('rsrq',lat, lng, colorrsrq, `RSRQ: ${rsrq}dB`,index);
                postPORTItems('rssi',lat, lng, colorrssi, `RSSI: ${rssi}dBm`,index);
                postPORTItems('snr',lat, lng, colorsnr, `SNR: ${snr}dB`,index);
                postPORTItems('latencia',lat, lng, colorlate, `LATENCIA: ${latencia}ms`,index);
            })

            finalRSRPObject.value = { "features" : finalRSRPArray.value, type : "FeatureCollection" };
            await setSourceAndLayer('rsrp','SourceRSRP',finalRSRPObject.value,'LayerRSRP');

            finalRSRQObject.value = { "features" : finalRSRQArray.value, type : "FeatureCollection" };
            await setSourceAndLayer('rsrq','SourceRSRQ',finalRSRQObject.value,'LayerRSRQ');

            finalRSSIObject.value = { "features" : finalRSSIArray.value, type : "FeatureCollection" };
            await setSourceAndLayer('rssi','SourceRSSI',finalRSSIObject.value,'LayerRSSI');

            finalSNRObject.value = { "features" : finalSNRArray.value, type : "FeatureCollection" };
            await setSourceAndLayer('snr','SourceSNR',finalSNRObject.value,'LayerSNR');

            finalLATENCIAObject.value = { "features" : finalLATENCIAArray.value, type : "FeatureCollection" };
            await setSourceAndLayer('latencia','SourceLATENCIA',finalLATENCIAObject.value,'LayerLATENCIA');


            clearRSRQLayerSource();
            clearRSSILayerSource();
            clearSNRLayerSource();
            clearLATENCIALayerSource();
            showCheckBoxes.value = true;
        }
        else {
            showError.value = true;
            showCheckBoxes.value = false;
        }
    })
    .catch(function (error) {
        if (error.response) {
            showError.value = true;
        } else if (error.request) {
            showError.value = true;
        } else {
            showError.value = true;
        }
    });
}

const postPORTItems = (tipo:string, latitud:number, longitud:number, color:string, valor:string, index:number) => {
    let finalITEM = {                        
            "type": "Feature",
            "properties": {
                "level": 0,
                "name": `AllGraph${tipo}`,
                "height": 100,
                "base_height": 0,
                "color": color,
                "center": [longitud,latitud],
                "valor": valor,
            },
            "geometry": {
                "coordinates": [ getFinalPositions(latitud,longitud) ],
                "type": "Polygon"
            },
            'id': index 
    }
    if(tipo.toUpperCase() === 'RSRP')       { finalRSRPArray.value.push(finalITEM) }
    if(tipo.toUpperCase() === 'RSRQ')       { finalRSRQArray.value.push(finalITEM) }
    if(tipo.toUpperCase() === 'RSSI')       { finalRSSIArray.value.push(finalITEM) }
    if(tipo.toUpperCase() === 'SNR')        { finalSNRArray.value.push(finalITEM) }
    if(tipo.toUpperCase() === 'LATENCIA')   { finalLATENCIAArray.value.push(finalITEM) }
}

const setSourceAndLayer = async (tipo:string, source:string, alldata:any, layer:string) => {
    const mySource = map.getSource(source);
    if(mySource === undefined){ 
        map.addSource(source, { 'type': 'geojson', 'data': alldata});        
        map.addLayer({
            'id': layer,
            'type': 'fill-extrusion',
            'source': source,
            'paint': {
                'fill-extrusion-color': ['get', 'color'],
                'fill-extrusion-height': ['get', 'height'],
                'fill-extrusion-base': ['get', 'base_height'],
                'fill-extrusion-opacity':1,
            }
        });
        
        const popup = new maplibregl.Popup({
            closeButton: false,
            closeOnClick: false
        });

        map.on('mouseenter', layer, (e:any) => {
            map.getCanvas().style.cursor = 'pointer';
            
            const coordinates:any = [e.lngLat.lng, e.lngLat.lat];
            const valor = e.features[0].properties.valor ;
            let mensaje = `${valor}` 

            while (Math.abs(e.lngLat.lng - coordinates[0]) > 180) {
                coordinates[0] += e.lngLat.lng > coordinates[0] ? 360 : -360;
            }

            popup.setLngLat(coordinates).setHTML(mensaje).addTo(map);
        });

        map.on('mouseleave', layer, () => {
            map.getCanvas().style.cursor = '';
            popup.remove();
        });
    }
    else{
        mySource.setData(alldata)
    }
}

const showCircles = async (firstDate:string, lastDate:string) => {
    circles.value = [];
    allCirclesFiltered.value.forEach((circle:CIRCLE) => {
        if(circle.fecha && new Date(circle.fecha).getTime() <= new Date(lastDate).getTime() && new Date(circle.fecha).getTime() >= new Date(firstDate).getTime()){
            circles.value.push(circle)
        }
    })
    showRangeSlider.value = true;
}

watch(modelSliderStatus,(a:number[], b:number[])=>{
    if(JSON.stringify(a)!==JSON.stringify(b)) {
        showCircles(arrayDatesCircle.value[a[0]],arrayDatesCircle.value[a[1]]);
        indexFrom.value = a[0];
        indexTo.value   = a[1];
    }
})

const seasonStatus = (val:number) => {
    let finalDate = arrayDatesCircle.value[val];
    return finalDate;
}

watch(getValueStoreDRIVE,(a:string, b:string)=>{
    a !== b ? resetData() : null;
    finalListTruck.value = haulTrucksList.value;
})

watch(selectedRSRP,(state:boolean)=>{
    if(state){
        setSourceAndLayer('rsrp','SourceRSRP',finalRSRPObject.value,'LayerRSRP');
    }else{
        clearRSRPLayerSource();
    }
})
watch(selectedRSRQ,(state:boolean)=>{
    if(state){
        setSourceAndLayer('rsrq','SourceRSRQ',finalRSRQObject.value,'LayerRSRQ');
    }else{
        clearRSRQLayerSource();
    }
})
watch(selectedRSSI,(state:boolean)=>{
    if(state){
        setSourceAndLayer('rsrs','SourceRSSI',finalRSSIObject.value,'LayerRSSI');
    }else{
        clearRSSILayerSource();
    }
})
watch(selectedSNR,(state:boolean)=>{
    if(state){
        setSourceAndLayer('snr','SourceSNR',finalSNRObject.value,'LayerSNR');
    }else{
        clearSNRLayerSource();
    }
})
watch(selectedLATENCIA,(state:boolean)=>{
    if(state){
        setSourceAndLayer('latencia','SourceLATENCIA',finalLATENCIAObject.value,'LayerLATENCIA');
    }else{
        clearLATENCIALayerSource();
    }
})

const clearRSRPLayerSource = () => {    
    if(map.getLayer('LayerRSRP')) { map.removeLayer('LayerRSRP'); }
    if(map.getSource('SourceRSRP')) { map.removeSource('SourceRSRP'); }    
}
const clearRSRQLayerSource = () => {    
    if(map.getLayer('LayerRSRQ')) { map.removeLayer('LayerRSRQ'); }
    if(map.getSource('SourceRSRQ')) { map.removeSource('SourceRSRQ'); }    
}
const clearRSSILayerSource = () => {    
    if(map.getLayer('LayerRSSI')) { map.removeLayer('LayerRSSI'); }
    if(map.getSource('SourceRSSI')) { map.removeSource('SourceRSSI'); }    
}
const clearSNRLayerSource = () => {    
    if(map.getLayer('LayerSNR')) { map.removeLayer('LayerSNR'); }
    if(map.getSource('SourceSNR')) { map.removeSource('SourceSNR'); }    
}
const clearLATENCIALayerSource = () => {    
    if(map.getLayer('LayerLATENCIA')) { map.removeLayer('LayerLATENCIA'); }
    if(map.getSource('SourceLATENCIA')) { map.removeSource('SourceLATENCIA'); }    
}

const resetData = () => {
    circles.value = [];
    arrayDatesCircle.value = [];
    haulTruckSelected.value = '';
    infoWindowVisible.value = false;
}

let map:any = '' ;

onMounted(async() => {
    getAllHaulTrucks();

    
    map  = new maplibregl.Map({
        container: 'map',
        center: [-110.34928166, 30.972732],
        zoom: 14,
        pitch: 40,
        bearing: 30,
        antialias: true,
        style: 'https://api.maptiler.com/maps/satellite/style.json?key=6JWVt6LFaY22nquimZpF',
    });

    map.addControl(new maplibregl.NavigationControl());
})

</script>
<style>
 
#map { height: 700px; }
.maplibregl-popup-content{
    color: black;
}

 .classalgo{
    position: absolute;
    top: 50%;
    width: 100px;
    content: '${e.name} ';
    color: black;
    margin-top: 20px; 
    font-size: 12px;
    line-height: 10px;
 }

 .sheetTooltip{
    border-radius: 10px;
    padding: 5px;
}
</style>