<template>
    <!-- <v-card v-if="ipInfo === m.ip" color="transparent" width="220"> -->
    <v-card color="transparent" elevation="0" width="220" class="text-center">
        <v-card-text class="pl-1"> 
            <v-row >
                <v-col cols="12" class="pa-0">
                    {{ nameItem }}   
                </v-col>
                <v-col cols="12" class="pa-0 pt-1">
                    <v-btn
                    block
                    color="bgCircular"
                    variant="plain"
                    size="small"
                    @click="showDialog(ip,typeItem,nameItem)"
                    >
                        {{ ip }}
                    </v-btn> 
                </v-col>                                    
            
                <v-col cols='12' class="pt-6">
                    <h5> {{ t('mapsPage.latency')}} </h5>                                       
                </v-col>
                
                <v-col cols="8" align-self="center" class="alineacion pa-0 pl-3">
                    <k-progress 
                        active  
                        :percent="100" 
                        :color="colorOne" 
                        bg-color="#d0d0d173"
                        type="lump" 
                        :cut-width="1" 
                        cut-color="#d0d0d173" 
                        :line-height="20"
                        :show-text="false"
                    >                                    
                    </k-progress>  
                    
                </v-col>
                <v-col class="pa-0 pr-2" cols='4' align-self="center">
                    {{ firstValue }}
                </v-col>

                <v-col cols='12' v-if="!isCost">
                    <h5> {{ t('mapsPage.available')}} </h5>                                        
                </v-col>
                <v-col cols="8" align-self="center" class="alineacion pa-0 pl-3" v-if="!isCost">
                    <k-progress 
                        active  
                        :percent="100" 
                        :color="colorTwo" 
                        bg-color="#d0d0d173"
                        type="lump" 
                        :cut-width="1" 
                        cut-color="#d0d0d173" 
                        :line-height="20"
                        :show-text="false"
                    >                                    
                    </k-progress>  
                    
                </v-col>
                <v-col class="pa-0 pr-2" cols='4' align-self="center" v-if="!isCost">
                    {{ secondValue }}%
                </v-col>

                <v-col cols='12' v-if="isCost">
                <h5> {{ t('mapsPage.cost')}} </h5>                                        
                </v-col>
                <v-col cols="8" align-self="center" class="alineacion pa-0 pl-3" v-if="isCost">
                    <k-progress 
                        active  
                        :percent="100" 
                        :color="colorTwo" 
                        bg-color="#d0d0d173"
                        type="lump" 
                        :cut-width="1" 
                        cut-color="#d0d0d173" 
                        :line-height="20"
                        :show-text="false"
                    >                                    
                    </k-progress>  
                    
                </v-col>
                <v-col class="pa-0 pr-2" cols='4' align-self="center" v-if="isCost">
                    {{ secondValue }}
                </v-col>
            </v-row>
        </v-card-text>
    </v-card>

    <v-dialog v-model="dialog"  width="85%" >
        <dialogComponent
        :ip="selectedIp"
        :typeSel="selectedType"
        :name="selectedHostName"
        @dialog="closeDialog"
        />        
    </v-dialog>
</template>

<script setup lang="ts">
import { ref } from 'vue';
import { useI18n }          from 'vue-i18n';

//Component
import dialogComponent      from '@/components/SmartLink/dialog/dialogComponent.vue'

defineProps({
    ip:{ type:String, required:true },
    nameItem:{ type:String, required:true },
    typeItem:{ type:String, required:true },
    colorOne:{ type:Array, required:false },
    colorTwo:{ type:Array, required:false },
    firstValue:{ type:Number, required:true },
    secondValue:{ type:Number, required:true },
    isCost:{ type:Boolean, value:false },
})
//==================================================//
//==================== VARIABLES ===================//
//==================================================//

/**
 * Vue / Vuetify
 */
const { t}                 = useI18n();
const dialog               = ref(false);
const selectedIp           = ref('');
const selectedHostName     = ref('');
const selectedType         = ref('');



const showDialog = (ip:string, type:string, name:string) =>{
    dialog.value            = !dialog.value;
    selectedHostName.value  = name;
    selectedIp.value        = ip;
    selectedType.value      = type;
}

const closeDialog = (value:boolean) => {
    dialog.value            = value
}

</script>