<template>
    <v-fab class="fabButtonFloat" :color="CONSTCOLOR.COLOR_HTML.green" icon="mdi-wechat" variant="flat" @click="dialog = true"></v-fab>

    <v-dialog v-model="dialog" width="auto" transition="dialog-bottom-transition">
      <v-card
      width="600"
      prepend-icon="mdi-chat-processing-outline"
      title="SmartLink IA Chat"
      class="borderCard"
      >

      <v-virtual-scroll 
      ref="virtualScroll"
      :items="textResponse"
      :max-height="500" 
      :min-height="200" > 
      <template v-slot:default="{ item }">
        <v-sheet  color="transparent">
          <v-row justify="end" class="ma-0">
            <v-sheet class="cardTextIA classanswer" max-width='80%'>
              {{ item.answer }}
            </v-sheet>
          </v-row>
          <v-row justify="start" class="ma-0">
            <v-icon class="mt-6 ml-3"> mdi-message-text</v-icon>
             <!-- <img :src="finalLOGO" :width="30" :height="30" class="mt-6 ml-3"/> -->
            <v-sheet class="cardTextIA pl-1" max-width='80%' color='transparent'>
              <!-- {{ item.response }} -->
                <TypingText :text="item.response" />
            </v-sheet>
          </v-row>
        </v-sheet>
      </template>
      </v-virtual-scroll>
      
      <template v-slot:actions>
        <v-textarea class="mx-2" v-model="textSearch" width="auto" variant="outlined" density="compact" rows="1" max-rows="2" :auto-grow="true"  hide-details />

        <v-tooltip :text="t('labels.clearChat')">
          <template v-slot:activator="{ props }">
            <v-btn
            v-bind="props"
            class="ms-auto"
            icon="mdi-chat-remove-outline"
            color="red"
            @click="clearChat"
          ></v-btn>
          </template>
        </v-tooltip>
        
        <v-btn
          class="ms-auto"
          icon="mdi-send-outline"
          color="green"
          @click="postPregunta"
          :disabled="textSearch === ''"
        ></v-btn>
      </template>

      </v-card>
    </v-dialog>
</template>

<script setup lang="ts">

import {ref, Ref} from 'vue';
import CONSTCOLOR from '@/constants/colors';
import axios      from 'axios';
import CONSTAPIS  from '@/constants/apis';
import TypingText from '@/components/PrincipalComponents/TypingText.vue';

import { CHATBOT } from '@/interfaces/SmartLink/IA'
import { useI18n } from 'vue-i18n';
// import CONSTIMAGES    from '@/constants/images';

const { t } = useI18n()
const dialog = ref(false);
const virtualScroll:Ref<InstanceType<typeof HTMLElement> | null> = ref(null)

const textSearch = ref('');
const textResponse:Ref<CHATBOT[]> = ref([]);

const postPregunta = () => {
  let params = {
    question : textSearch.value
  }
  axios.post(CONSTAPIS.APIS.chatGPT.preguntar, params).then(response => {
        let respuesta = JSON.parse(response.data.body);
        textResponse.value.push({
          answer: respuesta.pregunta,
          response: respuesta.respuesta.replace(/\n/g, `<br>`),
        }) 
        textSearch.value = '';
    })
}

const clearChat = () => {
  
  axios.get(CONSTAPIS.APIS.chatGPT.clear).then(response => {
        let respuesta = JSON.parse(response.data.body);
        console.log(respuesta)
        textResponse.value = []
        // textResponse.value.push({
        //   answer: respuesta.pregunta,
        //   resp onse: respuesta.respuesta.replace(/\n/g, `<br>`),
        // }) 
        // textSearch.value = '';
    })
}

</script>
<style>
 .fabButtonFloat{
    bottom: 35px;
    right: 65px;
    position: absolute;
    z-index: 1004;
    transform: translateY(0px);
    position: fixed;
 }
 .cardTextIA{
  border-radius: 10px;
  margin: 12px;
  /* margin-bottom: 12px; */
  padding: 12px;
 }

 .borderCard{
  border-radius: 20px !important;
 }

 .classanswer{
  background-color: rgba(255,255,255,0.3);
 }
</style>
